.get-started{
    display: inline-block;
    padding: 8px 36px;
    background: #ff6534;
    border-radius: 30px;
    font-family: 'ThicccBoi';
    font-size: 14px;
    font-weight: 600;
}
.nav{
    position: fixed;
    bottom: 0px;
    background: #282828;
    height: 68px;
    width: 100%;
    box-sizing: border-box;
    padding: 10px;
    text-align: right;
    display: flex;
    justify-content: space-between;

    .back{
        padding: 15px 50px;
        display: inline-block;
        font-family: 'DM Sans';
        font-weight: 900;
        font-size: 14px;
        color: #FFFFFF80;
    }

    .next{
        background: #FF6534;
        padding: 15px 50px;
        display: inline-block;
        margin-left: 10px;
        font-family: 'DM Sans';
        font-weight: 900;
        font-size: 14px;
        border-radius: 3px;
    }
}